import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			classData: {}, //课程数据
			classList: [], //课程列表
			currentPage: 1 //分页参数，第几页
		}
	},
	onLoad() {
		this.userLogin()
	},
	onShow() {
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.classList.length >= this.classData.total) {
			return;
		}
		this.currentPage++;
		this.getClassesList()
	},
	methods: {
		userLogin() {
			let that = this
			let baseUrl = 'https://fitness.bidou88.cn/api'
			uni.login({
			  success (res) {
			    if (res.code) {
					//这里直接用原生请求就行了
					uni.request({
						url: `${baseUrl}/v1/login/getToken`,
						data: {
						  code: res.code
						},
						success (res) {
							if(res.data.code != 0) {
								uni.showToast({
									title: res.data.msg,
									icon: 'none',
									duration: 2000
								});
								return;
							}
							console.log('授权成功')
							let loginResp = res.data.data;
							uni.setStorageSync('token', loginResp.token)
							that.$store.commit('updateToken', loginResp.token)
						},
						fail(res) {
							interfaceurl.showErr(res)
						}
					})
			    } else {
			      uni.showToast({
					  title: '登录失败！' + res.errMsg,
					  icon: 'none',
					  duration: 2000
			      });
			    }
			  }
			})
		},
		initData() {
			//重置分页参数
			this.classData = {}
			this.classList = []
			this.currentPage = 1
			this.getClassesList()
		},
		//获取首页课程列表
		getClassesList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.classesList, {page: 1}, false).then((res) => {
				that.classData = res.data
				if(that.currentPage == 1) {
					that.classList = res.data.data
				} else {
					that.classList = that.classList.concat(res.data.data)
				}
			});
		},
		turnCourseDetail(course) {
			this.$store.commit('setChooseCourse', course)
			uni.navigateTo({
			    url: '/pages/shop/course-detail'
			});
		}
	}
}
